<template>
  <div class="navBar" ref="navBar">

    <div class="logo">

      <router-link to="/">
        <img src="../../assets/images/logo/logo-dark.png" />
      </router-link>

        <button class="hamburger hamburger--squeeze" ref="hamburger" type="button" v-on:click="toggleMenuIcon()">
          <span class="hamburger-box"><span class="hamburger-inner"></span></span>
        </button>

    </div>

    <div class="menuMobile" ref="menuMobile">

      <div class="header"></div>

      <div class="main" ref="main">

        <div class="animate">
          <router-link to="/" class="point link">HOME</router-link>
        </div>

        <div class="animate">
          <router-link to="/hochzeitsfotografie" class="point link">FOTO</router-link>
        </div>

        <div class="animate">
          <router-link to="/hochzeitsvideografie" class="point link">VIDEO</router-link>
        </div>

        <div class="animate">
          <router-link to="/leistungen" class="point link">LEISTUNGEN</router-link>
        </div>

        <div class="animate">
          <router-link to="/about" class="point link">ABOUT</router-link>
        </div>

        <div class="animate">
          <router-link to="/kontakt" class="point link">KONTAKT</router-link>
        </div>

      </div>

      <div class="footer">
        <a href="https://www.facebook.com/fayaphotovideo/" target="_blank">
          <font-awesome-icon icon="fa-brands fa-facebook" size="1x" color="black" />
        </a>
        <a href="https://www.instagram.com/fayaphotovideo.de/" target="_blank">
          <font-awesome-icon icon="fa-brands fa-instagram" size="1x" color="black"  />
        </a>
        <a href="https://www.youtube.com/channel/UCFA95FXca117uT_Ssf1rdDg" target="_blank">
          <font-awesome-icon icon="fa-brands fa-youtube" size="1x" color="black"  />
        </a>

      </div>

    </div>

  </div>

</template>

<script>
export default {
  name: "NavBarMobileDark",
  data() {
    return {
      active: false,
    }
  },
  mounted() {
    // TODO: clean up
    let menu = document.getElementsByClassName('menuMobile')[0]
    menu.style.zIndex = "-1"

    /* CALCULATE & SET INITIAL VH VALUE */
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    /* ADD LISTENER FOR VH VALUE IN CASE OF WINDOW RESIZING */
    window.addEventListener('resize', () => {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    });
  },
  methods: {
    toggleMenuIcon() {
      let fullpage;
      // eslint-disable-next-line no-undef
      if (typeof fullpage_api != "undefined") {
        // eslint-disable-next-line no-undef
        fullpage = fullpage_api
      }

      let html_string = document.documentElement
      let body = document.body

      let ham = this.$refs["hamburger"]
      let menu = this.$refs["menuMobile"]
      let all_animate = document.getElementsByClassName('animate')
      let navBar = this.$refs["navBar"]

      if (typeof ham != "undefined" && ham.classList.contains("is-active")) {
        this.active = false
        html_string.style.overflow = "revert"
        body.style.overflow ="revert"
        ham.classList.remove("is-active")
        ham.classList.remove("open")
        menu.style.opacity = "0"
        let i = 0
        while (all_animate[i]) {
          all_animate[i].classList.remove('move')
          i++;
        }
        navBar.style.position = "relative"
        if (typeof fullpage != "undefined") {
          fullpage.setAllowScrolling(true)
          fullpage.setKeyboardScrolling(true)
        }
        menu.style.zIndex = "-1"
      } else {
        this.active = true
        html_string.style.overflow = "hidden"
        body.style.overflow ="hidden"
        navBar.style.position = "revert"
        ham.classList.add("is-active")
        ham.classList.add("open")
        menu.style.zIndex = "1000"
        menu.style.opacity = "1"
        menu.style.overflow = "hidden"
        if (typeof fullpage != "undefined") {
          fullpage.setAllowScrolling(false)
          fullpage.setKeyboardScrolling(false)
        }
        let i = 0
        while (all_animate[i]) {
          all_animate[i].classList.add('move')
          i++;
        }
      }
    },
  }
}

</script>

<style scoped>

.navBar {
  position: relative;
}

button {
  -webkit-tap-highlight-color: transparent;   /* removes blue background on click */
}

.logo {
  position: absolute;
  height: 15vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10000;
  background-color: transparent;
}

.logo a {
  display: flex;
  align-items: center;
}

.menuMobile {
  font-family: 'Glory', sans-serif;
  letter-spacing: 0.1em;
  display: block;
  position: absolute;
  background-color: black;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  width: 100%;
  margin: auto;
  opacity: 0;
  /* transition: opacity 1s; */
}

img {
  height: 10vh;
  width: 10vh;
  margin-left: 30px;
}

.header {
  height: 15vh;
  background-color: #F2EEEA;
}

.main {
  height: 70vh;
  height: calc(var(--vh, 1vh) * 70);
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  background-color: #F2EEEA;
  /* overflow: scroll;  use this when there are too many menu points */
}

.main div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0px;
  margin: 3px 0;
  cursor: default;

}

.point {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4vh;
}

.link:hover {
  cursor: pointer;
  color: darkgrey;
  text-shadow: black;
  transition: color 250ms;
}

.main div:nth-child(1) {
  animation-delay: 0.1ms;
}
.main div:nth-child(2) {
  animation-delay: 0.2s;
}
.main div:nth-child(3) {
  animation-delay: 0.3s;
}
.main div:nth-child(4) {
  animation-delay: 0.4s;
}
.main div:nth-child(5) {
  animation-delay: 0.5s;
}
.main div:nth-child(6) {
  animation-delay: 0.6s;
}

.move {
  animation-duration: 400ms;
  animation-fill-mode: both;
  animation-name: fadeInUp;
}

@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

.material-icons {
  font-size: 4vh;
  padding-right: 10px;
  transition: linear;
}

.footer {
  width: 100%;
  height: 15vh;
  height: calc(var(--vh, 1vh) * 15);
  background-color: #F2EEEA;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer a {
  font-size: 4vh;
  padding: 0 20px;
}

/* HAMBURGER ICON ONLY */

.hamburger {
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  height: 25px;
  background-color: transparent;
  border: 0;
  margin-right: 20px;
}

.hamburger:hover {
  opacity: 0.7;
}

.hamburger.is-active:hover {
  opacity: 0.7;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: black;
}

.hamburger-box {
  width: 25px;
  height: 5px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 25px;
  height: 1.5px;
  background-color: black;

  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before, .hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -8px;
}

.hamburger-inner::after {
  bottom: -8px;
}

/*
   * Squeeze
   */
.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}

.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 0;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}

.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}


@media screen and (min-width: 1024px) {

  .navBar {
    display: none;
  }

}

</style>